* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-decoration: none;
}
.container-header {
  width: 100%;
  margin: 0 auto;
}
header {
  background-color: black;
  width: 100%;
  height: 60px;
  display: flex;
  align-items: center;
}
.icons {
  display: flex;
  align-items: center;
  gap: 0 25px;
  padding-left: 210px;
}
.number {
  display: flex;
  align-items: center;
  gap: 0 10px;
  padding-left: 80px;
}
.number-p {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  display: flex;
  align-items: center;
  color: #ffffff;
}
.texts {
  display: flex;
  align-items: center;
  gap: 0 42px;
  padding-left: 650px;
}
.texts-p {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #ffffff;
}
