* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
.container-nav {
  width: 100%;
  margin: 0 auto;
  padding: 15px 0;
}
.logo {
  padding-left: 210px;
}
.card-nav {
  display: flex;
  align-items: center;
}
.texts-nav {
  padding-left: 300px;
}
.ul-nav {
  display: flex;
  align-items: center;
  gap: 0 53px;
}
.Link {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 30px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
}
.Link:hover {
  color: #5a5ac9;
}
.icons-nav {
  display: flex;
  align-items: center;
  gap: 0 15px;
  padding-left: 222px;
}
