* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Cart-container {
  width: 100%;
}
.cart {
  width: 100%;
  background: #f6f6f6;
  text-align: center;
}
.cart-texts {
  padding-top: 110px;
  padding-bottom: 110px;
}
.cart-h {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 38px;
  text-transform: capitalize;
  color: #111111;
  padding-bottom: 10px;
}
.cart-p {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: capitalize;
  color: #111111;
}
.cart-p2 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: capitalize;
  color: #5a5ac9;
}
.wrapper-cart {
  display: flex;
  align-items: center;
  gap: 0 10px;
  justify-content: center;
  padding-top: 30px;
}
.cart-line {
  border-top: 1px solid rgb(184, 178, 178);
  width: 1300px;
  padding-bottom: 35px;
  margin-left: 220px;
}
.cart-h2 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 38px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #111111;
  padding-top: 60px;
  padding-left: 220px;
  padding-bottom: 20px;
}
thead {
  height: 45px;
  display: flex;
  align-items: center;
}
.thead {
  background: #5a5ac9;
  margin-left: 220px;
}
.tbody {
  margin-left: 220px;
}
table {
  margin-bottom: 80px;
}
tbody tr th {
  width: 170px;
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  line-height: 26px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #212529;
}
tbody tr {
  display: flex;
  padding: 0 0px 0 100px;
}
thead tr {
  display: flex;
  gap: 0 105px;
  padding: 0 100px 0 100px;
}
thead tr th {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 28px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: capitalize;
  color: #ffffff;
}
.qty {
  width: 50px;
}
.cart-th1 {
  padding-left: 30px;
}
.cart-th3 {
  padding-left: 90px;
}
.cart-th4 {
  margin-left: 20px;
}
.cart-th5 {
  margin-left: -15px;
}
.cart-th6 {
  padding-left: 10px;
}
.cart-th7 {
  padding-left: 10px;
}
.total {
  width: 100px;
  height: 70px;
}
.total-p {
  padding-top: 10px;
  padding-left: 10px;
}
