* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.container-footer {
  background: #efefef;
  width: 100%;
}
footer {
  display: flex;
  padding-bottom: 100px;
}
.first-stand {
  padding-top: 80px;
  padding-left: 200px;
  padding-right: 150px;
}
.footer-p {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: #565656;
  width: 351px;
  padding-top: 24px;
}
.footer-icons {
  padding-top: 29px;
  display: flex;
  align-items: center;
  gap: 0 10px;
}
.second-stand {
  padding-top: 85px;
  padding-right: 120px;
}
.footer-h {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #111111;
  padding-bottom: 18px;
}
.second-stand img {
  padding-bottom: 24px;
}
.footer-p2 a {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #565656;
  padding-bottom: 5px;
}

.third-stand {
  padding-top: 85px;
  padding-right: 120px;
}
.footer-h2 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #111111;
  padding-bottom: 18px;
}
.third-stand img {
  padding-bottom: 24px;
}
.footer-p3 a {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #565656;
  padding-bottom: 5px;
}
.fourth-stand {
  padding-top: 85px;
}
.fourth-stand img {
  padding-bottom: 24px;
  padding-top: 18px;
}
.footer-h3 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #111111;
}
.footer-p3 a:hover {
  color: #5a5ac9;
}
.footer-p2 a:hover {
  color: #5a5ac9;
}
.footer-p4 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: #565656;
  width: 355px;
}
.footer-input {
  margin-top: 20px;
  outline: none;
  border: none;
  padding: 13px 80px 13px 11px;
  border-bottom-left-radius: 30px;
  border-top-left-radius: 30px;
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  display: flex;
  align-items: center;
  color: #565656;
}
.footer-btn {
  outline: none;
  border: none;
  padding: 15px 20px;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  background: #5a5ac9;
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  text-transform: capitalize;
  color: #ffffff;
  margin-top: 20px;
  cursor: pointer;
}
.footer-form {
  display: flex;
  align-items: center;
}
.footer-bottom {
  background-color: black;
  display: flex;
  gap: 0 740px;
}
.bottom-p {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: #ffffff;
  padding-left: 200px;
  padding-top: 15px;
  padding-bottom: 10px;
}
.bottom-p a {
  color: #5a5ac9;
}
