* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.Admin-container {
  width: 100%;
}
.admin {
  width: 100%;
  background: #f6f6f6;
  text-align: center;
}
.admin-texts {
  padding-top: 110px;
  padding-bottom: 110px;
}
.admin-h {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 38px;
  text-transform: capitalize;
  color: #111111;
  padding-bottom: 10px;
}
.admin-p {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: capitalize;
  color: #111111;
}
.admin-p2 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: capitalize;
  color: #5a5ac9;
}
.wrapper-admin {
  display: flex;
  align-items: center;
  gap: 0 10px;
  justify-content: center;
  padding-top: 30px;
}
.admin-line {
  border-top: 1px solid rgb(184, 178, 178);
  width: 1300px;
  padding-bottom: 35px;
  margin-left: 220px;
}
.admin-h2 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 700;
  font-size: 30px;
  line-height: 38px;
  display: flex;
  align-items: center;
  text-transform: capitalize;
  color: #111111;
  padding-top: 60px;
  padding-left: 220px;
  padding-bottom: 20px;
}
.admin-form {
  display: flex;
  flex-wrap: wrap;
  gap: 27px 15px;
  padding-left: 220px;
  padding-bottom: 80px;
}
.admin-input1 {
  padding: 10px 465px 10px 13px;
}
.admin-input2 {
  padding: 10px 465px 10px 13px;
}
.admin-input3 {
  padding: 10px 465px 80px 13px;
}
.admin-input4 {
  padding: 10px 465px 10px 13px;
}
.admin-btn1 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  padding: 15px 30px;
  border: none;
  background: #5a5ac9;
  cursor: pointer;
}
.admin-btn2 {
  font-family: sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 14px;
  display: flex;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #ffffff;
  padding: 15px 30px;
  border: none;
  background: #5a5ac9;
  cursor: pointer;
}
.admin-btns {
  display: flex;
  gap: 0 23px;
  margin-top: 26px;
}
